import React, { useState } from "react";
import { injectIntl } from "react-intl";
import {
  UncontrolledDropdown,
  DropdownItem,
  DropdownToggle,
  DropdownMenu
} from "reactstrap";
import { NavLink } from "react-router-dom";
import { connect } from "react-redux";
import {
  setContainerClassnames,
  clickOnMobileMenu,
  logoutUser,
  changeLocale
} from "../../redux/actions";
import {
  searchPath,
  isDarkSwitchActive
} from "../../constants/defaultValues";
import { MobileMenuIcon, MenuIcon } from "../../components/svg";
import TopnavDarkSwitch from "./Topnav.DarkSwitch";
import Profil from '../../assets/image/profil.png'


const TopNav = (props) => {
  const authLogin = props.authLogin
  const [state, setState] = useState({
    isInFullScreen: false,
    searchKeyword: ""
  })

  const isInFullScreenOK = () => {
    return (
      (document.fullscreenElement && document.fullscreenElement !== null) ||
      (document.webkitFullscreenElement &&
        document.webkitFullscreenElement !== null) ||
      (document.mozFullScreenElement &&
        document.mozFullScreenElement !== null) ||
      (document.msFullscreenElement && document.msFullscreenElement !== null)
    );
  };
  const removeEventsSearch = () => {
    document.removeEventListener("click", handleDocumentClickSearch, true);
  };
  const handleDocumentClickSearch = e => {
    let isSearchClick = false;
    if (
      e.target &&
      e.target.classList &&
      (e.target.classList.contains("navbar") ||
        e.target.classList.contains("simple-icon-magnifier"))
    ) {
      isSearchClick = true;
      if (e.target.classList.contains("simple-icon-magnifier")) {
        search();
      }
    } else if (
      e.target.parentElement &&
      e.target.parentElement.classList &&
      e.target.parentElement.classList.contains("search")
    ) {
      isSearchClick = true;
    }

    if (!isSearchClick) {
      const input = document.querySelector(".mobile-view");
      if (input && input.classList) input.classList.remove("mobile-view");
      removeEventsSearch();
      setState({
        searchKeyword: ""
      });
    }
  };

  const search = () => {
    props.history.push(searchPath + "/" + state.searchKeyword);
    setState({
      searchKeyword: ""
    });
  };

  const toggleFullScreen = () => {
    const isInFullScreen = isInFullScreenOK();

    var docElm = document.documentElement;
    if (!isInFullScreen) {
      if (docElm.requestFullscreen) {
        docElm.requestFullscreen();
      } else if (docElm.mozRequestFullScreen) {
        docElm.mozRequestFullScreen();
      } else if (docElm.webkitRequestFullScreen) {
        docElm.webkitRequestFullScreen();
      } else if (docElm.msRequestFullscreen) {
        docElm.msRequestFullscreen();
      }
    } else {
      if (document.exitFullscreen) {
        document.exitFullscreen();
      } else if (document.webkitExitFullscreen) {
        document.webkitExitFullscreen();
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen();
      } else if (document.msExitFullscreen) {
        document.msExitFullscreen();
      }
    }
    setState({
      isInFullScreen: !isInFullScreen
    });
  };

  const handleLogout = () => {
    props.logoutUser(props.history);
  };

  const menuButtonClick = (e, menuClickCount, containerClassnames) => {
    e.preventDefault();

    setTimeout(() => {
      var event = document.createEvent("HTMLEvents");
      event.initEvent("resize", false, false);
      window.dispatchEvent(event);
    }, 350);
    props.setContainerClassnames(
      ++menuClickCount,
      containerClassnames,
      props.selectedMenuHasSubItems
    );
  };
  const mobileMenuButtonClick = (e, containerClassnames) => {
    e.preventDefault();
    props.clickOnMobileMenu(containerClassnames);
  };
  const { containerClassnames, menuClickCount } = props;
  return (
    <nav className="navbar fixed-top">
      <div className="d-flex align-items-center navbar-left">
        <NavLink
          to="#"
          className="menu-button d-none d-md-block"
          onClick={e =>
            menuButtonClick(e, menuClickCount, containerClassnames)
          }
        >
          <MenuIcon />
        </NavLink>
        <NavLink
          to="#"
          className="menu-button-mobile d-xs-block d-sm-block d-md-none"
          onClick={e => mobileMenuButtonClick(e, containerClassnames)}
        >
          <MobileMenuIcon />
        </NavLink>
      </div>
      <a className="navbar-logo" href="/">
        <span className="logo d-none d-xs-block" />
        <span className="logo-mobile d-block d-xs-none" />
      </a>
      <div className="navbar-right">
        {isDarkSwitchActive && <TopnavDarkSwitch />}
        <div className="header-icons d-inline-block align-middle">
          <button
            className="header-icon btn btn-empty d-none d-sm-inline-block"
            type="button"
            id="fullScreenButton"
            onClick={toggleFullScreen}
          >
            {state.isInFullScreen ? (
              <i className="simple-icon-size-actual d-block" />
            ) : (
                <i className="simple-icon-size-fullscreen d-block" />
              )}
          </button>
        </div>
        <div className="user d-inline-block">
          <UncontrolledDropdown className="dropdown-menu-right">
            <DropdownToggle className="p-0" color="empty">
              <span className="name mr-1">
                {(authLogin && authLogin.klinik.namaklinik) && (authLogin.klinik.namaklinik)}
              </span>
              <span>
                <img alt="Profile" src={Profil} />
              </span>
            </DropdownToggle>
            <DropdownMenu className="mt-3" right>
              <DropdownItem onClick={() => props.history.push(`/app/profil`)}>Account  {authLogin ? (` (${authLogin.username})`) : ""}</DropdownItem>
              {(authLogin.jenisUser === 'pasien' || authLogin.superadmin) && (
                <DropdownItem onClick={() => props.history.push(`/app/manajemen-klinik`)}>Manajemen Klinik</DropdownItem>
              )}
              <DropdownItem divider />
              <DropdownItem onClick={() => handleLogout()}>
                Sign out
                </DropdownItem>
            </DropdownMenu>
          </UncontrolledDropdown>
        </div>
      </div>
    </nav>
  );
}


const mapStateToProps = ({ menu, settings }) => {
  const { containerClassnames, menuClickCount, selectedMenuHasSubItems } = menu;
  const { locale } = settings;
  return {
    containerClassnames,
    menuClickCount,
    selectedMenuHasSubItems,
    locale
  };
};
export default injectIntl(
  connect(
    mapStateToProps,
    { setContainerClassnames, clickOnMobileMenu, logoutUser, changeLocale }
  )(TopNav)
);
