import React, { Suspense, lazy } from 'react';
import { Route, withRouter, Switch, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';

import AppLayout from '../../layout/AppLayout';

const Gogo = lazy(() =>
  import('./gogo')
);
const Klinik = lazy(() =>
  import('./manajemenKlinik/ManajemenKlinik')
);
const SecondMenu = lazy(() =>
  import('./second-menu')
);
const Profil = lazy(() =>
  import('./profil/Profil')
);
const Dashboard = lazy(() =>
  import('./dashboard/')
);
const KonfirmasiJadwal = lazy(() =>
  import('./transaksi/konfirmasiJadwal/KonfirmasiJadwal')
);
const DaftarAntrian = lazy(() =>
  import('./transaksi/daftarAntrian/DaftarAntrian')
);
const Penjualan = lazy(() =>
  import('./transaksi/penjualan/')
);
const Pembelian = lazy(() =>
  import('./transaksi/pembelian/')
);
const StokTersedia = lazy(() =>
  import('./inventory/stokTersedia')
);
const StokMasuk = lazy(() =>
  import('./inventory/stokMasuk/')
);
const StokKeluar = lazy(() =>
  import('./inventory/stokKeluar/')
);
const Dokter = lazy(() =>
  import('./dataMaster/dokter/')
);
const Pasien = lazy(() =>
  import('./dataMaster/pasien/')
);
const Admin = lazy(() =>
  import('./dataMaster/admin/')
);
const JenisProduk = lazy(() =>
  import('./dataMaster/jenisProduk/')
);
const KategoriProduk = lazy(() =>
  import('./dataMaster/kategoriProduk/')
);
const Produk = lazy(() =>
  import('./dataMaster/produk/')
);
const Laporan = lazy(() =>
  import('./laporan/')
);
const BuatJanji = lazy(() =>
  import('./buatJanji/BuatJanji')
);
const Riwayat = lazy(() =>
  import('./riwayat/')
);

const LaporanPenjualan = lazy(() =>
  import('./laporan/penjualan')
);
const LaporanPelayanan = lazy(() =>
  import('./laporan/pelayanan')
);


const ListJanji = lazy(() =>
import('./listJanji/lisJanji')
);
//list-janji
const Layanan = lazy(() =>
import('./dataMaster/layanan/Layanan')
);


const App = (props) => {
  const authLogin = JSON.parse(localStorage.getItem('keminaAdmin'))
  const { match } = props;

  return (
    <AppLayout>
      <div className="dashboard-wrapper">
        <Suspense fallback={<div className="loading" />}>
          <Switch>
            {authLogin && authLogin.klinik.kodeklinik ? (
              <Redirect exact from={`${match.url}/`} to={`${match.url}/dashboard`} />
            ) : (
                <Redirect exact from={`${match.url}/`} to={`${match.url}/manajemen-klinik`} />

              )}
            <Route
              path={`${match.url}/manajemen-klinik`}
              render={props => <Klinik {...props} />}
            />
            <Route
              path={`${match.url}/gogo`}
              render={props => <Gogo {...props} />}
            />
            <Route
              path={`${match.url}/second-menu`}
              render={props => <SecondMenu {...props} />}
            />
            <Route
              path={`${match.url}/dashboard`}
              render={props => <Dashboard {...props} />}
            />
            <Route
              path={`${match.url}/profil`}
              render={props => <Profil {...props} />}
            />

            {/* transaksi */}
            <Route
              path={`${match.url}/transaksi/konfirmasi-jadwal`}
              render={props => <KonfirmasiJadwal {...props} />}
            />
            <Route
              path={`${match.url}/transaksi/daftar-antrian`}
              render={props => <DaftarAntrian {...props} />}
            />
            <Route
              path={`${match.url}/transaksi/penjualan`}
              render={props => <Penjualan {...props} />}
            />
            <Route
              path={`${match.url}/transaksi/pembelian`}
              render={props => <Pembelian {...props} />}
            />

            {/* inventory */}
            <Route
              path={`${match.url}/inventory/stok-tersedia`}
              render={props => <StokTersedia {...props} />}
            />
            <Route
              path={`${match.url}/inventory/stok-masuk`}
              render={props => <StokMasuk {...props} />}
            />
            <Route
              path={`${match.url}/inventory/stok-keluar`}
              render={props => <StokKeluar {...props} />}
            />

            {/* data master */}
            <Route
              path={`${match.url}/data-master/dokter`}
              render={props => <Dokter {...props} />}
            />
            <Route
              path={`${match.url}/data-master/pasien`}
              render={props => <Pasien {...props} />}
            />
            <Route
              path={`${match.url}/data-master/admin`}
              render={props => <Admin {...props} />}
            />
            <Route
              path={`${match.url}/data-master/jenis-produk`}
              render={props => <JenisProduk {...props} />}
            />
            <Route
              path={`${match.url}/data-master/kategori-produk`}
              render={props => <KategoriProduk {...props} />}
            />
            <Route
              path={`${match.url}/data-master/produk`}
              render={props => <Produk {...props} />}
            />
            <Route
              path={`${match.url}/data-master/layanan`}
              render={props => <Layanan {...props} />}
            />

            {/* laporan */}
            <Route
              path={`${match.url}/laporan`}
              render={props => <Laporan {...props} />}
            />

            {/* profil */}
            <Route
              path={`${match.url}/profil`}
              render={props => <Profil {...props} />}
            />

            {/* buat janji */}
            <Route
              path={`${match.url}/buat-janji`}
              render={props => <BuatJanji {...props} />}
            />

            {/* riwayat */}
            <Route
              path={`${match.url}/riwayat`}
              render={props => <Riwayat {...props} />}
            />

            {/* riwayat */}
            <Route
              path={`${match.url}/laporan-penjualan`}
              render={props => <LaporanPenjualan {...props} />}
            />

            {/* riwayat */}
            <Route
              path={`${match.url}/laporan-pelayanan`}
              render={props => <LaporanPelayanan {...props} />}
            />


              {/* Dokter */}
              <Route
              path={`${match.url}/list-janji`}
              render={props => <ListJanji {...props} />}
            />
            <Redirect to="/error" />
          </Switch>
        </Suspense>
      </div>
    </AppLayout>
  );
}
const mapStateToProps = ({ menu }) => {
  const { containerClassnames } = menu;
  return { containerClassnames };
};

export default withRouter(
  connect(
    mapStateToProps,
    {}
  )(App)
);
